import axios from 'axios'

// let baseUrl='http://172.18.226.41:3000'
let baseUrl='http://music.cpengx.cn'
// let baseUrl='https://neteasecloudmusicapi-docs.4everland.app/'
export function getBanner(type=1){
    return axios.get(`${baseUrl}/banner?type=${type}`)
}
export function getMusicList(limit=10){
    return axios.get(`${baseUrl}/personalized?limit=${limit}`)
}
export function getPlayListDetail(id){
    return axios.get(`${baseUrl}/playlist/detail?id=${id}`)
}
export function getPlayList(id){
    return axios.get(`${baseUrl}/song/url?id=${id}`)
}
export function getLyric(id){
    return axios.get(`${baseUrl}/lyric?id=${id}`)
}
export function searchMusic(keywords){
    return axios.get(`${baseUrl}/search?keywords=${keywords}`)
}
export function getUserInfo(loginInfo){
    return axios.get(`${baseUrl}/login/cellphone?phone=${loginInfo.phone}&password=${loginInfo.password}`)
}
export function getUserDetail(uid){
    return axios.get(`${baseUrl}/user/detail?uid=${uid}`)
}