<template>
<div class="musicList">
    <div class="listTop">
        <div class="topTitle">发现好歌单</div>
        <div class="seeMore">查看更多</div>
    </div>
    <div class="listBody">
        <swiper :modules="modules"     
        :slides-per-view="3"
        >
            <swiper-slide v-for="(item,i) in state.res2" :key="i">
                <router-link :to="{path:'/listview',query:{id:item.id}}">
                    <img :src="item.picUrl" :alt="item.name">
                    <br>
                    <span class="name">{{item.name}}</span>
                    <span class="playCount"><span class="iconfont icon-bofang"></span>{{changeCount(item.playCount)}}</span>
                </router-link>
            </swiper-slide>
        </swiper>
    </div>
</div>
</template>
<script>
import SwiperClass, { Pagination } from 'swiper'
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import 'swiper/css'
import 'swiper/css/pagination'
import { onMounted,reactive } from 'vue';
import {getMusicList} from '../assets/apifuntion'
export default {
    components: {
        Swiper,
        SwiperSlide,
    },
    setup(){
        let state =reactive({
            res2:[
                {id:''}
            ]
        });
        let changeCount=function(num){
            let cnum=num>100000000?(num/100000000).toFixed(1)+'亿':num>10000?(num/10000).toFixed(1)+'W':num;
            return cnum;
        };
        onMounted(async()=>{
            state.res2=await (await getMusicList()).data.result; 
        });
        return{
            state,
            changeCount,
            modules: [Pagination],
        };
    }
}    
</script>

<style lang="less" scoped>
.musicList{
    .listTop{
        display: flex;
        justify-content: space-between;
        height: 0.8rem;
        .topTitle{
            font-size: 0.4rem;
            font-weight: 900;
        }
        .seeMore{
            padding: 0 0.1rem;
            height: 0.6rem;
            border:1px solid #ccc;
            border-radius: 0.2rem;
            line-height: 28px;
            font-size: 0.25rem;
        }
    }
    .listBody{
        .swiper{
            width: 7.5rem;
            height: 3.15rem;
            .swiper-slide {
                position: relative;
                width: 3rem;
                height: 4rem;
                flex-direction: column;
                font-size: 18px;
                text-decoration: none;
                color: #000;
                font-weight: 600;
                a{
                    img {
                        margin: 0 0 0 0.2rem ;
                        width: 2.2rem;
                        height: 2.2rem;
                        border-radius: 0.4rem;
                        align-self: center;
                    }
                    .name {
                        width: 2rem;
                        height: 1rem;
                        font-size: 0.24rem;
                        line-height: 0.4rem;
                    }
                    .playCount{
                        position: absolute;
                        right: 0.2rem;
                        top: 0.05rem;
                        font-size: 0.3rem;
                        color: rgba(254, 252, 252);
                    }
                }
            }
        }
    }
}

</style>