<template>
    <div class="play">
            <div @click="show=!show" class="left">
                <img v-if="playlist[playCurrentIndex].al.picUrl" :src="playlist[playCurrentIndex].al.picUrl" alt="">
                <span v-else class="iconfont icon-heijiaochangpian"></span>
                <div class="content">
                    <div class="title">{{playlist[playCurrentIndex].name||'title'}}</div>
                    <div class="tips">横滑可以切换上下首哦</div>
                </div>
            </div>
        <div class="right">
            <span v-show="paused" class="iconfont icon-bofang" @click="playf"></span>
            <span v-show="!paused" class="iconfont icon-zanting" @click="playf"></span>        
            <span class="iconfont icon-qita"></span>        
        </div>
        <musicView 
            @back="show=!show" 
            v-show="show" 
            :playDetail="playlist[playCurrentIndex]" 
            :paused="paused"    
            :playf="playf"
            :show="show"
        >
        </musicView>
        <audio v-if="music.data.data[0].url" ref='audio' :src="music.data.data[0].url"></audio>
    </div>
</template>
<script>
import musicView from '@/views/musicView.vue';
import { watch, reactive,toRefs,getCurrentInstance} from 'vue'
import { mapState, useStore } from 'vuex';
export default{
    components:{
        musicView
    },
    computed:{
        ...mapState(['playlist','playCurrentIndex','music','InterValId','music']),
    },
    setup(){
        let store=useStore();
        let {proxy}=getCurrentInstance();
        let data=reactive({
            paused:true,
            show:false,
        });
        let playf=()=>{
            if(proxy.paused){
                proxy.$refs.audio.play();
                proxy.paused=false;
                setTimeout(function(){
                    proxy.updateTime();
                },1000)
            }else{
                proxy.$refs.audio.pause();
                proxy.paused=true;
                clearInterval(store.state.InterValId);
            };
        };
        let updateTime=()=>{
            if(proxy.$refs.audio.currentTime)
            store.state.InterValId=setInterval(()=>{ 
                console.log('',[proxy.$refs.audio][0].currentTime);
                store.commit('setCurrentTime',[proxy.$refs.audio][0].currentTime)
            },500)
        };
        let musicLoad=watch(
            ()=>[data.paused,data.show,store.state.playCurrentIndex],
            (value)=>{
                if(!(value[0])||value[1]){
                    store.dispatch('reqMusic',store.state.playlist[store.state.playCurrentIndex]);
                }else{
                    musicLoad();
                }
            },
        );
        let nextLyricAuto=watch(
            ()=>store.state.playCurrentIndex,
            ()=>{
                setTimeout(()=>{
                    proxy.$refs.audio.play();
                },1000)
            },
        );
        return{
            ...toRefs(data),
            proxy,
            store,playf,updateTime,musicLoad,nextLyricAuto
        }
    },
}
</script>
<style lang="less" scoped>
    .play{
        position: fixed;
        bottom: 0;
        left:0;
        z-index:1;
        background-color: #fff;
        width: 100%;
        height: 1rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-top: 1px #ccc;
        .left{
        display: flex;
        padding: 0 0.2rem;
        img{
            width: 0.8rem;
            height: 0.8rem;
            border-radius: 100%;
        }
        span{
            font-size: 0.9rem;
        }
        .content{
            padding: 0 0.2rem;
            .title{
                font-size: 300;
                color: #000;
            }
            .tips{
                font-size:0.2rem;
                color: #999;
            }
        }
        }
        .right{
            span{
                font-size:0.6rem;
            }
        }
    }
</style>