<template>

<div class="musicBody">
    <div class="bg" 
    :style="{backgroundImage: `url('${playDetail.al.picUrl}')`}"
    >
    </div>
    <div class="main">
        <div class="tops">
        <span class="iconfont icon-xitongfanhui" @click="$emit('back')"></span>
        <div class="title">
            <!-- <marquee behavior="scroll" direction="left"> -->
                {{playDetail.name}}
            <!-- </marquee> -->
        </div>
        <span class="iconfont icon-fenxiang"></span>
    </div>
    <div class="content" v-if="!ly" @click="ly=!ly">
        <img class="needle" :class="{active:paused}" src=".././assets/needle-ab.png" alt="">
        <img class="disc" src=".././assets/d7e4e3a244701ee85fecb5d4f6b5bd57.png" alt="">
        <img class="pic" :src="playDetail.al.picUrl" alt="">
    </div>
    <div ref='lyric' class="lyric" v-if="ly" @click="ly=!ly">
        <div class="top"></div>
        <p
            :class="{active:currentTime*1000<item.pre&&currentTime*1000>item.msTime}"
            v-for="(item,i) in lyricList" 
            :key="i"
        >
        {{item.lyric}}
        </p>
        <div class="foot"></div>
    </div>
    <div class="musicPanel">
        <div class="panelBtn">
            <span class="iconfont icon-24gl-repeat2"></span>
            <span class="iconfont icon-24gl-previous" @click="goplay(-1)"></span>
            <span v-show='paused' class="play iconfont icon-bofang" @click="playf"></span>
            <span v-show='!paused' class="play iconfont icon-zanting" @click="playf"></span>
            <span class="iconfont icon-24gl-next" @click="goplay(1)"></span>
            <span class="iconfont icon-danlieliebiao"></span>
        </div>
    </div>

    </div>
</div>
</template>
<script>
import { reactive,toRefs,watch,getCurrentInstance, onUpdated } from 'vue';
import {useStore,mapState, mapGetters, mapMutations} from 'vuex'
export default {
    props: ['playDetail', 'playf', 'paused','show'],
    name: 'musicview',
    computed:{
        ...mapState(['playlist','playCurrentIndex','lyric','InterValId','currentTime']),
        ...mapGetters(['lyricList']),
        ...mapMutations(['setCurrentTime'])
    },
    setup(){
        let store=useStore();
        let data=reactive({
            ly:true,
        });
        let {proxy}=getCurrentInstance();
        let goplay=(num)=>{
            console.log('切换歌曲');
            let index=proxy.playCurrentIndex+num
            index=index<0?proxy.playlist.length-1:index;
            store.commit('setPlayIndex',index);
            store.dispatch('reqLyric',proxy.playlist[index]);
            store.dispatch('reqMusic',proxy.playlist[index]);
        }
        let lyricload=watch(
            ()=>[data.ly,proxy.show],
            (value)=>{
                console.log('获取歌词');
                if(value[0]&&value[1]){
                    store.dispatch('reqLyric',proxy.playDetail) ;   
                }{
                    lyricload();
                }
            }
        );
        watch(
            ()=>store.state.currentTime,
            ()=>{
                console.log('歌词滚动');
                if(proxy.show&&data.ly){
                    let pp=document.querySelector('p.active');
                    if(pp!==null){
                        let p=[pp][0].offsetTop;
                        let h=(proxy.$refs.lyric.offsetHeight)/2;
                        proxy.$refs.lyric.scrollTop=(p-h);
                    }
                }
            }
        );
        return{
            ...toRefs(data),
            proxy,goplay,lyricload,
        }
    }
}
</script>
<style lang="less" scoped>

.musicBody {
position: fixed;
top: 0;
left: 0;
width: 100vw;
height: 100vh;
    .bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-size: auto 100%;
    background-position: center;
    background-color: #000;
    filter: blur(10px);
    }
    .main{
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        background-color: rgba(0,0,0,0.1);
        .tops {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        height: 1.2rem;
        position: absolute;
        left: 0;
        top: 0;
        color: #fff;
        padding: 0 0.4rem;

        .iconfont {
            font-size: 0.5rem;
            color: #fff;
        }
        }
        .content {
        position: absolute;
        width: 100%;
        height: 7.5rem;
        left: 0;
        top: 1.5rem;

        .needle.active {
            width: 2rem;
            position: absolute;
            left: 3rem;
            transform-origin: 0.3rem 0;
            transform: rotate(-20deg);
            z-index: 10;
            transition: all 1s;
        }

        .needle {
            width: 2rem;
            position: absolute;
            left: 3rem;
            transform-origin: 0.3rem 0;
            transform: rotate(-2deg);
            z-index: 10;
            transition: all 1s;
        }

        .disc {
            width: 4.5rem;
            height: auto;
            position: absolute;
            left: calc(50% - 2.25rem);
            top: 2rem;
        }

        .pic {
            position: absolute;
            left: calc(50% - 1.4rem);
            top: 2.85rem;
            width: 2.8rem;
            height: 2.8rem;
            border-radius: 100%;
            z-index: 1;
        }
        }
        .lyric{
        position: absolute;
        width: 100%;
        height: 7.5rem;
        left: 0;
        top: calc(50% - 3.75rem);
        overflow: scroll;
        text-align: center;
        color: #fff;
        .top,.iconfont{
            width: 100%;
            height: 50%;
        }
        .active{
            color:red;
            font-size: 0.45rem;
            transition: all 0.5s;
        }
        }
        .musicPanel {
        width: 100%;
        position: fixed;
        left: 0;
        top: calc(100% - 1.5rem);
        .panelBtn {
            width: 100%;
            height: 1rem;
            display: flex;
            justify-content: space-around;
            align-items: center;

            span {
                color: #fff;
                font-size: 0.8rem;
            }
            .play{
                color: #fff;
                font-size: 1rem;
            }
        }
        }
    }
}
</style>