import { createStore } from 'vuex'
import { getLyric,getPlayList,getUserInfo,getUserDetail } from '@/assets/apifuntion'
import { reactive } from 'vue';
export default createStore({
  state: {
    playlist:[
      {
        al:{
        id:29597,
        name:"有点野",
        pic:74766790689775,
        picUrl:"http://p4.music.126.net/vu3Cdo_dPq8HKOPI6_YXfA==/74766790689775",
        },
        name:'屋顶',
        id:29803531,
      }
    ],
    playCurrentIndex:0,
    lyric:{
      data:{
        lrc:{
          lyric:''
        }
      }
    },
    music:{
      data:{
        data:[
          {
            url:"http://m801.music.126.net/20220921085447/1de6dc3483565c6f5a355e1f4c082698/jdymusic/obj/wo3DlMOGwrbDjj7DisKw/14096603784/5c41/2d73/b063/8c4f510e1b47b022b7adbaebcec9014d.mp3"
          }
        ]
      }
    },
    currentTime:0,
    InterValId:0,
    userInfo:reactive({
      phone:'',
      password:'',
      account:{
        data:{
          level:0,
          listenSongs:0,
          peopleCanSeeMyPlayRecord:true,
          createDays:0,
          profile:{
            nickname:'未登录'
          }
        }
      },
    }),
    result:reactive({
      data:{
        account:{
          userName:'未登录'
        }
      }
    }),
    nowLogin:false,
    isLogin:false,
  },
  getters: {
    lyricList(state){
      let arr=state.lyric.data.lrc.lyric.split(/\n/igs).map((item,i)=>{
        let test=item.split(/\]/igs);
        let min=test[0].slice(1,3);
        let sec=test[0].slice(4,6);
        let mill=test[0].split(/\./igs)[1];
        return{
          min,
          sec,
          mill,
          msTime:parseInt(min)*60*1000+parseInt(sec)*1000+parseInt(mill),
          lyric:test[1],
        }
      })
      arr.forEach((item,i)=>{
        if(i==arr.length-1){
          item.pre=0;
        }else{
          item.pre=arr[i+1].msTime;
        }
      })
      return arr
    }
  },
  mutations: {
    setNowLogin(state,value){
      state.nowLogin=value;
    },
    setPlayList(state,value){
      state.playlist=value
    },
    setPlayIndex(state,value){
      state.playCurrentIndex=value
    },
    setLyric(state,value){
      state.lyric=value
    },
    setMusic(state,value){
      state.music=value
    },
    setCurrentTime(state,val){
      state.currentTime=val
    },
    setUserInfo(state,value){
      state.userInfo.password=value[1].password;
      state.userInfo.phone=value[1].phone;
      state.result=value[0]
      state.isLogin=true;
      state.nowLogin=false;
    },
    setUserAccount(state,value){
      state.userInfo.account=value;
    }
  },
  actions: {
    async reqLyric(context,value){
      let result=await getLyric(value.id);
      context.commit('setLyric',result)      
    },
    async reqMusic(context,value){
      let result=await getPlayList(value.id);
      context.commit('setMusic',result);
    },
    async reqUserInfo(context,value){
      let result=await getUserInfo(value);
      if(result.data.code==200){
        context.commit('setUserInfo',[result,value]);
        let detail=await getUserDetail(result.data.account.id);
        if(detail.data.code==200){
          localStorage.userData=JSON.stringify(detail)
          console.log('detail:',detail);
          context.commit('setUserAccount',detail);
        }
      }
    },
  },
  modules: {
  }
})
