<template>
  <swiper 
    :modules="modules" 
    :pagination="{ clickable: true }"
  >
      <swiper-slide v-for="(item,i) in res" :key="i">
        <img :src="item.pic" alt="test">
      </swiper-slide>
  </swiper>
</template>

<script>
import SwiperClass, { Pagination } from 'swiper'
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'

// import swiper module styles
import 'swiper/css'
import 'swiper/css/pagination'
import { onMounted,ref } from 'vue';
import {getBanner} from '../assets/apifuntion'
// more module style...

  export default {
    components: {
      Swiper,
      SwiperSlide
    },
    setup() {
      var res=ref([
        {pic:require('../assets/1.jpg')},
        {pic:require('../assets/1.jpg')},
        {pic:require('../assets/1.jpg')},
        {pic:require('../assets/1.jpg')},
        {pic:require('../assets/1.jpg')},
        {pic:require('../assets/1.jpg')},
        {pic:require('../assets/1.jpg')},
      ]);
      onMounted(async()=>{
        let answer={};
        answer=await getBanner(1);
        res.value=answer.data.banners;
      });
      return {
        res,
        modules: [Pagination]
      } 
    },
    
  } 
</script>

<style lang="less" scoped>
  .swiper-slide{
    padding: 0.2rem;
    height: 3rem;
    img{
      border-radius: 0.3rem;
      width: 100%;
    }
  }
</style>